import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {AuthManagerProvider} from "../../providers/auth-manager/auth-manager";
import {GuiManagerProvider} from "../../providers/gui-manager/gui-manager";

/**
 * Generated class for the PasswordResetPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-password-reset',
  templateUrl: 'password-reset.html',
})
export class PasswordResetPage {

  private isRequest = false;
  private resetMail = '';
  private passOne = '';
  private passTwo = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, private authProvider: AuthManagerProvider, private guiManager:GuiManagerProvider) {
    const action = navParams.get('ac');

    this.isRequest = action == "request";

  }

  reset() {

  }

  /**
   * Requests a password reset from the backend.
   */
  requestReset() {

    this.authProvider.requestPasswordReset(this.resetMail, (success) => {

      if (success) {

        this.guiManager.showToast("Es wurde eine Email mit weiteren Schritten versendet", 10000)
      } else {

        this.guiManager.showToast("Fehler beim versenden der Email", 10000)
      }
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PasswordResetPage');
  }

}



